.sidebar-toggle-wrapper {
  transition: transform .3s ease-in-out;
  position: fixed;
  z-index: 5;
  top: 30px;
  right: 30px;
  width: 44px;
  height: 44px;
  overflow: hidden;
  text-indent: 100%;
  white-space: nowrap;
  cursor: pointer;
  border-radius: 99px;
  background-color: #ffffff;
}

.sidebar-toggle-wrapper.open {
  transition: transform .3s ease-in-out;
  transform: translateX(250px);
  z-index: 9999;
}

.sidebar-toggle-wrapper:hover .sidebar-toggle {
  background-color: #666;
}

.sidebar.open,
.sidebar-toggle-wrapper.open {
  transform: translateX(-14rem);
  z-index: 9999;
}

.sidebar-toggle {
  position: absolute;
  z-index: 6;
  top: 48%;
  left: 50%;
  margin-left: -10px;
  display: block;
  width: 20px;
  height: 2px;
  background: #333333;
  transition: background 0.3s;
}

.sidebar-toggle:before, .sidebar-toggle:after {
  content: '';
  position: absolute;
  left: 0;
  background: inherit;
  width: 100%;
  height: 100%;
  transform: translateZ(0);
  backface-visibility: hidden;
  transition: transform 0.5s, background 0s;
}

.sidebar-toggle:before {
  top: -6px;
  transform: rotate(0);
}

.sidebar-toggle:after {
  bottom: -6px;
  transform: rotate(0);
}

.sidebar-toggle-wrapper.open {
  position: fixed;
  background-color: #333333;
}

.sidebar-toggle-wrapper.open .sidebar-toggle {
  background: transparent;
}

.sidebar-toggle-wrapper.open .sidebar-toggle:before, .sidebar-toggle-wrapper.open .sidebar-toggle:after {
  background: #ffffff;
}

.sidebar-toggle-wrapper.open .sidebar-toggle:before {
  top: 0;
  transform: rotate(135deg);
}

.sidebar-toggle-wrapper.open .sidebar-toggle:after {
  bottom: 0;
  transform: rotate(225deg);
}

