body {
	font-size:80%;
	@include MQ(M) {
		font-size:90%;
	}
	@include MQ(L) {
		font-size:100%;
	}
}



h1,
h2,
h3,
h4,
h5,
h6 {
	margin-top: 1em;
	margin-bottom: .25em;
  font-family: $heading-font-family;
  font-weight: $heading-font-base-weight;
}

h1 {
	font-size:2.5em;
}

h2 {
	font-size:2em;
	&.sidebar {
		font-size:1.2em;
		margin-top:0px;
	}
}

h3 {
	font-size:1.6em;
}

h4 {
	font-size:1.3em;
}

h5 {
	font-size:1em;
}

p {
	font-size:1em;
	margin-bottom:1em;
}