// -----------------------------------------------------------------------------
// This file contains styles that are specific to the home page.
// -----------------------------------------------------------------------------
body#home {
  background-color: #cccccc;
  margin-top: 0px;
}

header.home-header {
  display: flex;
  flex-direction: column;
  background-color: white;
  justify-content: center;
  align-items: center;
  min-height: 14em;
  @include MQ(M) {
    min-height: 20em;
  }
  div.home-logo {
    @include center;
    text-indent: -99em;
    overflow-x: hidden;
    height: 8em;
    width: 14em;
  }
  .home-tagline {
    max-width: 90%;
  }
}

.site-logo {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url('/assets/icons/site-logo.svg');
  &.darkbg {
    background-image: url(/assets/icons/site-logo-white.svg);
  }
}

ul.homepage-list {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  @include MQ(M) {
    margin-top: -2em;
  }
  @include flexlistlayout($gutter: 15px);
  li {
    @include card($bgcolor: white);
    h1 {
      font-size: 1.5em;
      margin: 0px;
    }
  }
  li:first-child {
    @include MQ(M) {
      font-size: 1.6em;
    }
  }
}
