@charset "UTF-8";

html {
  box-sizing: border-box;
  font-family: $base-font-family;
}

body {
  color: $base-font-color;
  font-family: $base-font-family;
  font-weight: $base-font-weight;
  border:none;
  margin:0px;
  padding:0px;
  outline:none;
  min-height:100%;
  min-width:100%;
  min-height:100vh;
  min-width:100vw;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}


/**
 * Basic styles for links
 */

a {
  color: $link-color;
  text-decoration: none;
  @include on-event {
    color: $link-color-event;
    text-decoration: $link-event-text-decoration;
  }
}

img {
  width: 100%;
  height: auto;
}

ul,
li {
  list-style: none;
  list-style-type: none;
  margin: 0px;
  padding: 0px;
}
